import React, { Component } from 'react';
import Oshwlogo from './oshwlogo.js';
import Downloader from './downloader.js';
import { Row, Col, Input, Navbar, NavItem } from 'react-materialize';
import './App.css';

class App extends Component {
  constructor(){
    super();
    this.state = {
      schematic : true,
      pcb : true,
      firmware : true,
      mech : true,
      designdocs : true,
      bom : true,
      commercial : true,
      style : 'original',
      partialS: false,
      partialP: false,
      partialF: false,
      partialM: false,
      partialD: false,
      partialB: false,
      partialC: false,
      imageWidth : 400,
      imageHeight : 400,
      davemod : true,
      davemodBottom : false,
      oshwunmask : true,
      fontSize : 20,
      fontFamily : '\'Open Sans\', Helvetica Neue, Arial, Helvetica, sans-serif'
    };
    this.handleOptionChange = this.handleOptionChange.bind(this)
    this.handlePartialOptionChange = this.handlePartialOptionChange.bind(this)
    this.handleDaveModChange = this.handleDaveModChange.bind(this)
    this.handleStyleChange = this.handleStyleChange.bind(this)
  }
  handleOptionChange(val) {
    this.setState({[val.target.value] : val.target.checked})
  }
  handlePartialOptionChange(val) {
    this.setState({[val.target.value] : val.target.checked})
  }
  handleDaveModChange(val) {
    console.log(val.target.value)
    if (val.target.value === 'davemod'){
      this.setState({
        davemod: true,
        davemodBottom: false,
        schematic : true,
        pcb : true,
        firmware : true,
        mech : true,
        designdocs : true,
        bom : true,
        commercial : true,
       })
    } else if (val.target.value === 'davemodBottom'){
      this.setState({
        davemod: false,
        davemodBottom: true,
        schematic : true,
        pcb : true,
        firmware : true,
        mech : true,
        designdocs : true,
        bom : true,
        commercial : true,
      })
    } else {
      this.setState({
        davemod: false,
        davemodBottom: false,
        schematic : false,
        pcb : false,
        firmware : false,
        mech : false,
        designdocs : false,
        bom : false,
        commercial : false,
     })

    }
  }
  handleStyleChange(val) {
    if (val.target.value === 'light'){
      document.getElementById("logo").style.cssText = 'background-color: #333333'
    } else {
      document.getElementById("logo").style.cssText = 'background-color: #FFFFFF'
    }
    if (val.target.value === 'light' || val.target.value === 'outline'){
      this.setState({
        style: val.target.value,
        fontSize : 17,
        fontFamily : '\'Open Sans Condensed\', Helvetica Neue, Arial, Helvetica, sans-serif'
      })
    } else {
      this.setState({
        style: val.target.value,
        fontSize : 20,
        fontFamily : '\'Open Sans\', Helvetica Neue, Arial, Helvetica, sans-serif'
      })
    }
  }
  render() {
    return (
      <div className="App">
        <Navbar brand="OSHW Logo Generator" color="orange" right>
          <NavItem href="https://github.com/matthewbadeau/OSHW-Logo-Autogen">Fork on GitHub</NavItem>
        </Navbar>
        <Row>
          <Col s={12} />
        </Row>
        <Row>
          <Col s={3} className="Logo offset-s3" id="logo">
              <Oshwlogo id="oshwlogo"
                schematic={this.state.schematic ? (this.state.partialS ? 's': 'S') : ""}
                pcb={this.state.pcb ? (this.state.partialP ? 'p': 'P') : ""}
                firmware={this.state.firmware ? (this.state.partialF ? 'f': 'F') : ""}
                mech={this.state.mech ? (this.state.partialM ? 'm': 'M') : ""}
                designdocs={this.state.designdocs ? (this.state.partialD ? 'd': 'D') : ""}
                bom={this.state.bom ? (this.state.partialB ? 'b': 'B') : ""}
                commercial={this.state.commercial ? (this.state.partialC ? 'c': 'C') : ""}
                style={this.state.style}
                davemod={this.state.davemod}
                oshwunmask={this.state.oshwunmask}
                />
          </Col>
          <Col s={6} className="Options">
            <Row>
              <Row>
              <Input
                type="checkbox"
                defaultChecked="true"
                name="options"
                value="schematic"
                label="Schematic"
                onChange={this.handleOptionChange}
                s={4}
                />
              <label>Partial</label>
              <Input
                type="switch"
                value="partialS"
                className="offset-s3"
                onChange={this.handlePartialOptionChange}
                s={8}
                />
              </Row>
              <Row>
              <Input
                type="checkbox"
                defaultChecked="true"
                name="options"
                value="pcb"
                label="PCB"
                onChange={this.handleOptionChange}
                s={4}
                />
              <label>Partial</label>
              <Input
                type="switch"
                value="partialP"
                className="offset-s3"
                onChange={this.handlePartialOptionChange}
                s={8}
                />
              </Row>
              <Input
                type="checkbox"
                defaultChecked="true"
                name="options"
                value="firmware"
                label="Firmware"
                onChange={this.handleOptionChange}
                s={4}
                />
              <label>Partial</label>
              <Input
                type="switch"
                value="partialF"
                className="offset-s3"
                onChange={this.handlePartialOptionChange}
                s={8}
                />
              </Row>
              <Row>
              <Input
                type="checkbox"
                defaultChecked="true"
                name="options"
                value="mech"
                label="Mechanical / CAD Files"
                onChange={this.handleOptionChange}
                s={4}
                />
              <label>Partial</label>
              <Input
                type="switch"
                value="partialM"
                className="offset-s3"
                onChange={this.handlePartialOptionChange}
                s={8}
                />
              </Row>
              <Row>
              <Input
                type="checkbox"
                defaultChecked="true"
                name="options"
                value="designdocs"
                label="Design Documents"
                onChange={this.handleOptionChange}
                s={4}
                />
              <label>Partial</label>
              <Input
                type="switch"
                value="partialD"
                className="offset-s3"
                onChange={this.handlePartialOptionChange}
                s={8}
                />
              </Row>
              <Row>
              <Input
                type="checkbox"
                defaultChecked="true"
                name="options"
                value="bom"
                label="Bill of Materials"
                onChange={this.handleOptionChange}
                s={4}
                />
              <label>Partial</label>
              <Input
                type="switch"
                value="partialB"
                className="offset-s3"
                onChange={this.handlePartialOptionChange}
                s={8}
                />
              </Row>
              <Row>
              <Input
                type="checkbox"
                defaultChecked="true"
                name="options"
                value="commercial"
                label="Commercial Allowed"
                onChange={this.handleOptionChange}
                s={4}
                />
              <label>Partial</label>
              <Input
                type="switch"
                value="partialC"
                className="offset-s3"
                onChange={this.handlePartialOptionChange}
                s={8}
                />
              </Row>
              <Row>
              <Input
                name="proposalSelect"
                type="radio"
                value="davemod"
                label="Dave Jone's Video Suggestion"
                defaultChecked="true"
                onChange={this.handleDaveModChange}
                />
              <Input
                name="proposalSelect"
                type="radio"
                value="davemodBottom"
                label="Bottom of logo"
                onChange={this.handleDaveModChange}
                />
              <Input
                name="proposalSelect"
                type="radio"
                value="allTextOff"
                label="All off"
                onChange={this.handleDaveModChange}
                />
            </Row>
            <Row>
              Open Source Hardware text
              <Input
                type="switch"
                defaultChecked="true"
                name="oshwunmask"
                value="oshwunmask"
                onChange={this.handleOptionChange}
                s={12}
                />
            </Row>
            <div>
              <Input
                name="logoselect"
                type="radio"
                value="original"
                label="Original"
                defaultChecked="true"
                onChange={this.handleStyleChange}
                />
              <Input
                name="logoselect"
                type="radio"
                value="outline"
                label="Outline"
                onChange={this.handleStyleChange}
                />
              <Input
                name="logoselect"
                type="radio"
                value="light"
                label="Outline-Light"
                onChange={this.handleStyleChange}
                />
            </div>
            <Downloader options={this.state}/>
          </Col>
        </Row>
        <Row>
          <Col s={12} m={6} l={3} className="offset-s6">
            
          </Col>
        </Row>
      </div>
    );
  }
}

export default App;



